.header-image,
.header-image-interna{
    position: relative;
    width: 100%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    clear: both;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: top;

    .site-branding {
        max-width: 80%;
        text-align: center;
        z-index: 99;
        .custom-logo-link{
            margin-bottom: .5em;
            height: 35px;
            width: auto;
            img{
                height: 65px;
                width: auto;
            }
        }
        .site-branding-text{
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: auto;
            font-size: .8em;
            font-family: $font__sans;


            @media screen and (min-width: $query__small){
                font-size: 1em;
            }
        }
        .site-title {
            margin: 0;
            padding: 0;
            font-size: 1.6em;
            font-weight: 900;
            line-height: 1em;
            color: $color__navy;

            a {
                color:$color__navy;
                text-decoration: none;
                &:hover,
                    &:focus {
                    text-decoration: none;
                }
            }
        }

        .site-description {
            margin: 0;
            font-size: .9em;
            font-style: italic;
            font-weight: 100;
            color: $color__navy;
        }
    }
}

.header-image{
    height: 80vh;
    //max-height: 60vh;
    @media screen and (min-width: $query__small){
        height: 90vh;
    }
}
.header-image-interna{
    height: 100px;
    @media screen and (min-width: $query__small){
        height: 150px;
    }
    .site-branding {
        .custom-logo-link{
            display: none;
        }
    }
}



/*--------------------------------------------
** Barra superior que contiene el menu
--------------------------------------------*/

.site-header {
    position: -webkit-sticky;
    top: 0;
    z-index: 1;
    display: block;
    font-family: $font__sans;
    font-size: 90%;
    font-weight: 400;
    color: $color__gray;
    background-color: $color__navy;
    height: auto;
    @media screen and (min-width: $query__small) {
        padding: 0 .5em;

    }
}

@media screen and (min-width: $query__small){
    .site-header{

    }

    .site-branding{
        width: 50%;
        flex: 1 0 auto;
    }
    .site-navigation{
        width: 55%;
    }
}

/*---------------------------------
Header Menu
----------------------------------*/
@import "header-menu";
