.comment-navigation,
.posts-navigation,
.post-navigation,
.paging-navigation {
    padding: 80px 0;
    font-family: $font__sans;
    font-size: 80%;
    text-align: left;
    overflow: hidden;

    a {
        display: block;
        width: 100%;
        text-decoration: none;
        padding: 1em;
        border: 1px solid hsl(0, 0%, 80%);
        color: black;
        background: #E4E4E4;
        color: #333;

        &:hover,
            &:focus {
            color: white;
            border-color: $color__interactive;
            background: $color__interactive;
        }
    }

}


.nav-links {
    margin: 0 auto;
    padding: 0 .4em;
}

.nav-previous {
    
    padding-bottom: 1em;
    
    .meta-nav:before{
        content: "\2190";
        margin-right: .5em;
    }
}
.nav-next{
    .meta-nav:after{
        content: "\2192";
        margin-left: .5em;
    }
}

@media screen and (min-width: $query__medium) {

    .nav-links {
        //display: flex;
        //justify-content: space-between;
        //align-items: center;
        padding: 0;
        width: 100%;
    }

    .comment-navigation,
    .posts-navigation,
    .post-navigation {

        .nav-previous {
            float: left;
            width: 48%;
        }

        .nav-next {
            float: right;
            text-align: right;
            width: 48%;
        }
    }

    .nav-previous {
        padding-bottom: 0;
    }


}

.post-navigation {

    .meta-nav {
        display: block;
    }

    @media screen and (min-width: 30em) {
        padding-bottom: 1em;
    }

    .post-title {
        font-size: 1.2em;
        font-weight: 700;
        border-bottom: 3px solid $color__interactive;
    }

    a:hover .post-title,
    a:focus .post-title{
        border-bottom-color: white;
    }

} 


.pagination {

    font-family: $font__sans;

    .nav-links {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }

    a,
    span {
        padding: .5em 1em;
        background: white;
    }

    a {
        text-decoration: none;

        &:focus,
        &:hover {
            color: white;
            background: $color__interactive;
        }
        
        &.prev:before{
            content: "\2190";
            margin-right: .5em;
        }
        &.next:after{
            content: "\2192";
            margin-left: .5em;
        }
    }

    .current {
        font-weight: 800;
        color: $color__interactive;
    }
}