p {
    margin-top: .5em;
    margin-bottom: 1.5em;
}

dfn, cite, em, i {
    font-style: italic;
}

blockquote {
    position: relative;
    margin: 0 0 2em;
    background: $color__gray;
    padding: 1em 2em;
    font-style: italic;

}

address {
    margin: 0 0 1.5em;
}

pre {
    background: $color__background-pre;
    font-family: $font__pre;
    @include font-size(0.9375);
    line-height: $font__line-height-pre;
    margin-bottom: 1.6em;
    max-width: 100%;
    overflow: auto;
    padding: 1.6em;
}

code, kbd, tt, var {
    font-family: $font__code;
    @include font-size(0.9375);
}

abbr, acronym {
    border-bottom: 1px dotted $color__border-abbr;
    cursor: help;
}

mark, ins {
    background: $color__background-ins;
    text-decoration: none;
}

big {
    font-size: 125%;
}

.post-content{
    ::-moz-selection { /* Code for Firefox */
        color: white;
        background: black;
    }
    ::selection {
        background: yellow;
    }
}
